<template>
  <div style="position: static">
    <!--    <div class="header">-->
    <!--      <div class="doc-title">-->
    <!--        <h2></h2>-->
    <!--        <h1>&nbsp;&nbsp;</h1>-->
    <!--      </div>-->
    <!--    </div>-->
    <div id="title"><b></b> &nbsp</div>
    <p></p>
    <div id="text">
      <b>
        <p></p>
        <p>
        <p>
        <p></p>
      </b>

    </div>
    <div id="d3Chart"></div>


  </div>
</template>

<script>


// import * as d3 from "../../assets/js/d3.v7.min.js";
import * as d3Sankey from "../../assets/js/d3-sankey.min.js";


// import {sankey as Sankey} from "d3-sankey";
import * as d3 from "d3";
// import {sankey} from "d3-sankey";

// import * as d3 from 'd3'
// import * as d3Sankey from 'd3-sankey'

export default {
  name: "Emotion",
  // data() {
  //   this.sankey = undefined
  // },
  mounted() {
    // d3.select("#d3Chart").remove();
    this.draw()
  }
  ,
  methods: {
    draw() {
      const data = {
        nodes: [
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''},
          {name: ''}
        ],
        links: [
          {source: '', target: '', value: 8.6},
          {source: '', target: '', value: 9.35},
          {source: '', target: '', value: 15},
          {source: '', target: '', value: 10.9},
          {source: '', target: '', value: 5.4},
          {source: '', target: '', value: 6.2},
          {source: '', target: '', value: 9.45},
          {source: '', target: '', value: 11.5},
          {source: '', target: '', value: 12.15},
          {source: '', target: '', value: 12.15},
          {source: '', target: '', value: 19.7},
          {source: '', target: '', value: 14.25},
          {source: '', target: '', value: 4.9},
          {source: '', target: '', value: 11.95},
          {source: '', target: '', value: 14},
          {source: '', target: '', value: 4.45},
          {source: '', target: '', value: 8.55},
          {source: '', target: '', value: 7.8},
          {source: '', target: '', value: 9.1},
          {source: '', target: '', value: 12.85},
          {source: '', target: '', value: 7.4},
          {source: '', target: '', value: 10.6},
          {source: '', target: '', value: 16.45},
          {source: '', target: '', value: 11.4},
          {source: '', target: '', value: 1.55},
          {source: '', target: '', value: 15.9},
          {source: '', target: '', value: 9.6},
          {source: '', target: '', value: 5.45},
          {source: '', target: '', value: 0.9},
          {source: '', target: '', value: 1.8},
          {source: '', target: '', value: 10.95},
          {source: '', target: '', value: 7.95},
          {source: '', target: '', value: 3.65},
          {source: '', target: '', value: 10.95},
          {source: '', target: '', value: 1.35},
          {source: '', target: '', value: 10.7},
          {source: '', target: '', value: 1.8},
          {source: '', target: '', value: 2.7},
          {source: '', target: '', value: 6.95},
          {source: '', target: '', value: 7.65}
        ]
      }
      // 
      const width = 700
      const height = 700
      const margin = 30
      // d3.select("#d3Chart").removeAll();
      // d3.select("#d3Chart").selectAll("svg").remove();
      const svg = d3
          .select('#d3Chart')
          .append('svg')
          .attr('width', width)
          .attr('height', height)
          .style('background-color', '') //#1a3055

      // 
      const chart = svg.append('g')
          .attr('transform', `translate(${margin}, ${margin})`)

      const colorScale = d3.scaleOrdinal(d3.schemeSet3)
      const sankey = d3Sankey
          .sankey()
          .nodeWidth(30)
          .nodePadding(20)
          .size([width - 2 * margin, height - 2 * margin])
          .nodeId((d) => d.name)

      const {nodes, links} = sankey({
        nodes: data.nodes,
        links: data.links
      })

      chart
          .append('g')
          .selectAll()
          .data(nodes)
          .join('g')
          .attr('class', 'node')
          .attr('indexName', (d) => d.name)
          .append('rect')
          .attr('fill', (d, i) => colorScale(d.name))
          .attr('x', (d) => d.x0)
          .attr('y', (d) => d.y0)
          .attr('height', (d) => d.y1 - d.y0)
          .attr('width', (d) => d.x1 - d.x0)
          .append('title')
          .text((d) => `${d.name}`)

      chart
          .append('g')
          .attr('fill', 'none')
          .selectAll()
          .data(links)
          .join('path')
          .attr('indexName', (d) => d.source.name + '-' + d.target.name)
          .attr('d', d3Sankey.sankeyLinkHorizontal())
          .attr('stroke', (d, i) => colorScale(d.source.name))
          .attr('stroke-width', (d) => d.width)
          .attr('stroke-opacity', '0.5')
          .append('title')
          .text((d) => `${d.value.toLocaleString()}`)

      chart
          .selectAll('.node')
          .append('text')
          .attr('class', 'text')
          .attr('x', (d) => (d.x0 + d.x1) / 2)
          .attr('y', (d) => (d.y0 + d.y1) / 2)
          .attr('stroke', '#000000')
          .attr('text-anchor', 'middle')
          .attr('dy', 6)
          .text((d) => d.name)

      d3.selectAll('.node')
          .on('mouseover', function (e, d) {
            d3.selectAll('.node, path').attr('fill-opacity', '0.1').attr('stroke-opacity', '0.1')

            d3.selectAll('[indexName*=' + d.name + ']')
                .attr('fill-opacity', '1')
                .attr('stroke-opacity', '0.6')
          })
          .on('mouseleave', function () {
            d3.selectAll('.node, path').attr('fill-opacity', '1').attr('stroke-opacity', '0.5')
          })

      // 
      d3.selectAll('path')
          .on('mouseover', function (e, d) {
            d3.selectAll('.node, path').attr('fill-opacity', '0.1').attr('stroke-opacity', '0.1')

            const hoverNodes = d3.select(e.target).attr('stroke-opacity', '0.5').attr('indexName').split('-')

            hoverNodes.forEach((name) => {
              d3.selectAll('[indexName=' + name + ']').attr('fill-opacity', '1')
            })
          })
          .on('mouseleave', function () {
            d3.selectAll('.node, path').attr('fill-opacity', '1').attr('stroke-opacity', '0.5')
          })
    }
  }
}
</script>

<style scoped>
h2 {
  font-family: ;
  font-size: 50px;
  font-style: italic;
  font-weight: bold;
}

h1 {
  font-family: ;
  font-size: 28px;
  font-style: normal;
  font-weight: lighter;
  font-variant: small-caps;
}
</style>
